@import url(https://fonts.googleapis.com/css?family=Fredoka+One&display=swap);
* {
	box-sizing: border-box;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  background-color: #fdfdff;
  font-family: 'Fredoka One', cursive;
  color: #4d6787; }

h1 {
  margin: 50px 0;
  text-align: center;
  font-size: 45px; }

.game {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; }

.game-reset {
  padding: 20px;
  background-color: #fefeff;
  border: 2px solid #d6e6ea;
  border-radius: 30px;
  font-family: inherit;
  font-size: 18px;
  color: #91a2ab;
  text-transform: uppercase;
  cursor: pointer;
  outline: transparent; }
  .game-reset.reset-disabled {
    opacity: 0.4;
    pointer-events: none; }

.square {
  width: 90px;
  height: 90px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer; }
  .square:nth-child(even) {
    border-left: 2px solid #d6e6ea;
    border-right: 2px solid #d6e6ea; }
  .square:hover {
    background-color: #ecfcfc; }

.square-icon {
  height: 70px;
  width: 70px;
  -webkit-transition: 500ms;
  transition: 500ms;
  opacity: 0; }
  .square-icon.icon-o {
    border: 10px solid #39bdd7;
    border-radius: 100%;
    opacity: 1; }
  .square-icon.icon-x {
    position: relative;
    color: #d9dcdf;
    -webkit-transform: translateX(calc(50% - 5px));
            transform: translateX(calc(50% - 5px));
    opacity: 1; }
    .square-icon.icon-x::before, .square-icon.icon-x::after {
      content: '';
      display: block;
      position: absolute;
      height: inherit;
      width: 10px;
      background-color: #3e89cf;
      border-radius: 15px; }
    .square-icon.icon-x::before {
      -webkit-transform: rotate(-45deg);
              transform: rotate(-45deg); }
    .square-icon.icon-x::after {
      -webkit-transform: rotate(45deg);
              transform: rotate(45deg); }

.board {
  margin: 50px 20px; }

.board-row {
  display: flex; }
  .board-row:nth-child(even) {
    border-top: 2px solid #d6e6ea;
    border-bottom: 2px solid #d6e6ea; }

.turn {
  display: flex;
  align-items: center;
  justify-content: space-around;
  position: relative;
  width: 100px;
  height: 50px;
  background-color: #fff;
  border: 2px solid #f4f6f6;
  border-radius: 50px; }
  .turn-icon {
    width: calc(50% - 4px);
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: 1;
    -webkit-transition: ease-in 0.4s;
    transition: ease-in 0.4s;
    color: #3a86c9; }
    .turn-icon.is-active {
      color: #fff; }
  .turn-toggle {
    height: 46px;
    width: 46px;
    position: absolute;
    top: 0;
    background-color: #39bcd4;
    border-radius: 50%;
    -webkit-transition: ease-out 0.4s;
    transition: ease-out 0.4s; }
    .turn-toggle.toggle-x {
      left: 0; }
    .turn-toggle.toggle-o {
      left: 50px; }

.result {
  margin: 0;
  font-size: 25px; }

.winner {
  text-transform: uppercase;
  font-weight: bold;
  font-size: 1.5em;
  vertical-align: middle; }

